.page-header {
  background-color: rgba(0, 0, 0, 0.3);
  margin-bottom: 23px;
  color: #ffffff;
  z-index: 11;
  position: relative;
}
@media (min-width: 660px) {
  .page-header {
    margin-bottom: 60px;
  }
}
@media (min-width: 1200px) {
  .page-header {
    margin-bottom: 57px;
  }
}
.page-header.is-open,
.page-header--no-js {
  background-color: #283645;
}
@media (min-width: 1200px) {
  .page-header.is-open,
  .page-header--no-js {
    background-color: transparent;
  }
}
.page-header.is-open .page-header__nav-toggle::before {
  top: 10px;
  right: -5px;
  width: 32px;
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header.is-open .page-header__nav-toggle::after {
  top: 10px;
  right: -5px;
  width: 32px;
  transform: rotate(-45deg);
}
.page-header.is-open .page-header__nav-toggle:hover::before,
.page-header.is-open .page-header__nav-toggle:hover::after,
.page-header.is-open .page-header__nav-toggle:active::before,
.page-header.is-open .page-header__nav-toggle:active::after {
  box-shadow: none;
}
.page-header.is-open ul,
.page-header--no-js ul {
  display: block;
  width: 100%;
}
@media (min-width: 1200px) {
  .page-header.is-open ul,
  .page-header--no-js ul {
    display: flex;
    position: static;
  }
}
.page-header--no-js .page-header__nav-toggle {
  display: block;
}
@media (min-width: 1200px) {
  .page-header--no-js .page-header__nav-toggle {
    display: none;
  }
}
.page-header--no-js .page-header__nav {
  position: static;
}
.page-header__wrapper {
  display: block;
}
@media (min-width: 1200px) {
  .page-header__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background-color: transparent;
    padding: 16px 18px 30px;
  }
}
.page-header__logo {
  width: 150px;
  height: 100%;
  margin-right: 50px;
  flex-shrink: 0;
  display: block;
  padding: 22px 20px 18px 20px;
}
.page-header__logo .page-header__logo-icon {
  fill: #ffffff;
}
@media (min-width: 660px) {
  .page-header__logo {
    padding: 27px 20px 41px 20px;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    display: flex;
    align-items: baseline;
    margin-right: 174px;
    padding: 0;
    width: 147px;
  }
}
.page-header__logo:hover svg {
  fill: #d22856;
}
.page-header__logo:active svg {
  fill: #ffffff;
  opacity: 0.3;
}
