  .page-header__logo-icon--tablet {
    display: block;
  }
}
@media (min-width: 1200px) {
  .page-header__logo-icon--tablet {
    display: none;
  }
}
.page-header__logo-icon--desktop {
  display: none;
}
@media (min-width: 1200px) {
  .page-header__logo-icon--desktop {
    display: block;
  }
}
.page-header__logo-icon--pink1200 {
  display: none;
}
@media (min-width: 1200px) {
  .page-header__logo-icon--pink1200 {
    display: block;
    margin-left: 12px;
  }
}
.page-header__nav-toggle {
  position: absolute;
  top: -44px;
  right: 20px;
  width: 50px;
  height: 24px;
  border: none;
  padding: 0;
  background-color: transparent;
}
@media (min-width: 660px) {
  .page-header__nav-toggle {
    top: -64px;
    right: 20px;
  }
}
@media (min-width: 1200px) {
  .page-header__nav-toggle {
    display: none;
  }
}
.page-header__nav-toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 2px;
  border-radius: 2px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header__nav-toggle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 2px;
  border-radius: 2px;
  background-color: #ffffff;
}
.page-header__nav-toggle:hover::before,
.page-header__nav-toggle:hover::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header__nav-toggle:active::before,
.page-header__nav-toggle:active::after {
  opacity: 0.3;
}
